<style lang=less>
@import "../../../assets/index.less";
@import "./invitecode.less";
</style>
<template>
    <div class="base-info invitecode">
        <div class="beijing">
            <img src="../../../assets/img/center/mine_img_9.png"/>
        </div>
        <ul class="invheader">
            <li class="list">
                <section class="list_left">
                    <img src="../../../assets/img/center/mine_img_6.png" alt=""/>
                </section>
                <section class="list_rigth">
                    <p class="one">
                        {{$t('center.invitecode[0]')}}
                    </p>
                    <p class="two">
                        {{mydowndata.ttl}}
                    </p>
                </section>
            </li>
            <li class="list">
                <section class="list_left">
                    <img src="../../../assets/img/center/mine_img_7.png" alt=""/>
                </section>
                <section class="list_rigth">
                    <p class="one">
                        {{$t('center.invitecode[1]')}}
                    </p>
                    <p class="two">
                        {{mydowndata.today_yongjin}}
                    </p>
                </section>
            </li>
            <li class="list">
                <section class="list_left">
                    <img src="../../../assets/img/center/mine_img_8.png" alt=""/>
                </section>
                <section class="list_rigth">
                    <p class="one">
                        {{$t('center.invitecode[2]')}}
                    </p>
                    <p class="two">
                        {{mydowndata.total_yongjin}}
                    </p>
                </section>
            </li>
        </ul>
        <section class="invfooter">
            <div class="left">
                <div class="qrcode">
                    <div id="qrcodeImg">
                    </div>
                </div>
                <p class="invtitle">{{$t('center.invitecode[3]')}}</p>
            </div>
            <div class="rigth">
                <p class="linf">{{userInfo.tgno}}</p>

                <div class="copyBtn" :data-clipboard-text="userInfo.tgno">{{$t('center.invitecode[4]')}}</div>
            </div>
        </section>
    </div>
</template>
<script>
import QRCode from "qrcodejs2";
// import html2canvas from 'html2canvas';
import ClipboardJS from "clipboard";
import { my_down, link } from "@/api/user.js";
export default {
  data() {
    return {
      mydowndata: {},
      userInfo: {}
    };
  },
  watch: {},
  methods: {
    getLink() {
      link().then(r => {
        if (r.data.status == 200) {
          this.$nextTick(() => {
            this.createQrcode(r.data.data.url);
          });
        }
      });
    },
    getdata() {
      let _this = this;
      my_down({ p: 1, size: "15" }).then(res => {
        _this.mydowndata = res.data.data;
      });
      _this.userInfo = JSON.parse(window.sessionStorage.getItem("userInfo"));
      // _this.createQrcode(_this.userInfo.tgno);
      _this.duplicate();
    },
    createQrcode(text) {
      // 生成二维码
      let qrcodeImgEl = document.getElementById("qrcodeImg");
      qrcodeImgEl.innerHTML = "";
      let qrcode = new QRCode(qrcodeImgEl, {
        width: 110,
        height: 110,
        colorDark: "#000000",
        colorLight: "#ffffff",
        correctLevel: QRCode.CorrectLevel.H
      });
      qrcode.makeCode(text);
    },
    duplicate() {
      let _this = this;
      var clipboard = new ClipboardJS(".copyBtn");

      clipboard.on("success", function(e) {
        e.clearSelection();
        _this.$message({
          message: _this.$t("center.invitecode[5]"),
          type: "success"
        });
      });

      clipboard.on("error", function(e) {
        _this.$message({
          message: _this.$t("center.invitecode[6]"),
          type: "warning"
        });
      });
    }
  },
  created() {
    this.getLink();
  },
  mounted() {
    this.getdata();
  }
};
</script>



